import React from 'react';
import PropTypes from 'prop-types';
import { graphql, Link } from 'gatsby';
import { Helmet } from 'react-helmet';
import { MDXProvider } from '@mdx-js/react';
import { MDXRenderer } from 'gatsby-plugin-mdx';
import Emoji from 'a11y-react-emoji';
import Layout from '../../components/Layout';
import Seo from '../../components/Seo';

import * as SingleBlogStyles from './single-blog.module.scss';

const URI_FRAGMENT = 'blog';

function SingleBlogSeo({ pageMeta }) {
  const {
    title,
    description,
    uri,
    publishedDate,
    modifiedDate,
    siteName,
    siteUrl,
    image,
    social,
  } = pageMeta;

  const schema = {
    '@context': 'https://schema.org/',
    '@graph': [
      {
        '@type': 'BreadcrumbList',
        '@id': siteUrl,
        name: siteName,
        itemListElement: [
          {
            '@type': 'ListItem',
            position: 1,
            name: 'Blog',
            item: `${siteUrl}/${URI_FRAGMENT}/`,
          },
          {
            '@type': 'ListItem',
            position: 2,
            name: title,
          },
        ],
      },
      {
        '@type': 'BlogPosting',
        mainEntityOfPage: {
          '@type': 'WebPage',
          '@id': `${siteUrl}/${uri}/`,
        },
        headline: title,
        description,
        author: {
          '@type': 'Person',
          name: siteName,
          url: siteUrl,
          sameAs: [social.linkedinUrl, social.githubUrl],
          jobTitle: 'Web Developer',
        },
        image,
        datePublished: publishedDate,
        dateModified: modifiedDate,
      },
    ],
  };
  const schemaString = JSON.stringify(schema);

  return (
    <Helmet>
      <meta property="og:type" content="article" />
      <meta property="article:author" content={siteName} />
      <meta property="article:published_time" content={publishedDate} />
      <meta property="article:modified_time" content={modifiedDate} />
      <script type="application/ld+json">{schemaString}</script>
    </Helmet>
  );
}

SingleBlogSeo.propTypes = {
  pageMeta: PropTypes.shape({
    title: PropTypes.string.isRequired,
    description: PropTypes.string.isRequired,
    uri: PropTypes.string.isRequired,
    publishedDate: PropTypes.string.isRequired,
    modifiedDate: PropTypes.string.isRequired,
    siteName: PropTypes.string.isRequired,
    siteUrl: PropTypes.string.isRequired,
    image: PropTypes.string.isRequired,
    social: PropTypes.object.isRequired,
  }),
};

export default function SingleBlogPage({ pageContext, data }) {
  const edge = data.allMdx.edges.find(({ node }) => node.id === pageContext.id);
  const post = edge.node;
  const metadata = data.site.siteMetadata;
  const {
    title,
    description,
    publishedDate,
    modifiedDate,
    prettyPublishedDate,
    prettyModifiedDate,
    images,
  } = post.frontmatter;
  const pageMeta = {
    title,
    description,
    uri: `${URI_FRAGMENT}/${post.slug.slice(0, -1)}`, // slug has trailing slash
    publishedDate,
    modifiedDate,
    siteName: metadata.title,
    siteUrl: metadata.siteUrl,
    image: metadata.image, // TODO fix with conditional for blog image
    social: metadata.social,
  };
  const hasUpdated = modifiedDate !== publishedDate;
  const shortcodes = { Emoji };

  return (
    <Layout>
      <Seo pageMeta={pageMeta} />
      <SingleBlogSeo pageMeta={pageMeta} />
      <article className={SingleBlogStyles.article}>
        <header>
          <h1>{title}</h1>
          <p>
            <time dateTime={publishedDate}>
              Published: {prettyPublishedDate}
            </time>
            {hasUpdated && (
              <>
                <br />
                <time dateTime={modifiedDate}>
                  Modified: {prettyModifiedDate}
                </time>
              </>
            )}
          </p>
        </header>
        <MDXProvider components={shortcodes}>
          <MDXRenderer localImages={images}>{post.body}</MDXRenderer>
        </MDXProvider>
        <footer>
          <div>
            {edge.previous && (
              <article>
                <p>{'<-- Previous post'}</p>
                <Link to={`/${URI_FRAGMENT}/${edge.previous.slug}`}>
                  <h3>{edge.previous.frontmatter.title}</h3>
                </Link>
              </article>
            )}
          </div>
          <div className={SingleBlogStyles.next}>
            {edge.next && (
              <article>
                <p>{'Next post -->'}</p>
                <Link to={`/${URI_FRAGMENT}/${edge.next.slug}`}>
                  <h3>{edge.next.frontmatter.title}</h3>
                </Link>
              </article>
            )}
          </div>
        </footer>
      </article>
    </Layout>
  );
}

SingleBlogPage.propTypes = {
  pageContext: PropTypes.shape({
    id: PropTypes.string.isRequired,
  }).isRequired,
  data: PropTypes.object.isRequired,
};

export const query = graphql`
  query SingleBlogPage {
    allMdx(
      sort: { fields: frontmatter___publishedDate, order: DESC }
      filter: { frontmatter: { published: { eq: true } } }
    ) {
      edges {
        previous {
          frontmatter {
            title
          }
          slug
        }
        node {
          frontmatter {
            title
            description
            publishedDate
            prettyPublishedDate: publishedDate(formatString: "MMM DD, YYYY")
            modifiedDate
            prettyModifiedDate: modifiedDate(formatString: "MMM DD, YYYY")
            images {
              childImageSharp {
                gatsbyImageData
              }
            }
          }
          slug
          body
          id
        }
        next {
          frontmatter {
            title
          }
          slug
        }
      }
    }
    site {
      siteMetadata {
        title
        siteUrl
        image
        social {
          linkedinUrl
          githubUrl
        }
      }
    }
  }
`;
